@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    font-family: 'Lato', 'Helvetica Neue', sans-serif;
    background-color: #F4F4F7;
}

h1 {
    font-family: 'Anton', sans-serif;
}

a.link-unstyled {
    color: inherit;
}

a.link-unstyled:hover {
    color: inherit;
    text-decoration: none;
}

.btn {
    padding: 0.375rem 2rem;
}

.btn.btn-primary {
    color: #fff;
    background-color: #7DCD40;
    border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
    color: #fff;
    background-color: #7DCD40;
    border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #7DCD40;
    border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
    box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
    color: #fff;
    background-color: #CD4040;
    border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
    color: #fff;
    background-color: #CD4040;
    border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #CD4040;
    border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
.Navbar {
    width: 100%;
    padding: 0.5rem 0;
    background-image: linear-gradient(to right, #161616, #191b1f, #182128, #13272f, #0a2e33, #073335, #063837, #083d37, #08413b, #074540, #074a44, #064e49);
}

.Navbar__brand {
    color: #ffffff;

    display: flex;
    align-items: center;
}

.Navbar__brand:hover {
    color: #ffffff;
    text-decoration: none;
}

.Navbar__brand-logo {
    margin-right: 0.5rem;
}
.Home {
    width: 100%;
    min-height: calc(100vh - 60px);
    background: url(/csswoman/platzi_badges/static/media/stars.877428a1.svg), #1B1B25;
    background-repeat: repeat;

    color: #ffffff;

    display: flex;
    align-items: center;
}

.Home__col {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media screen and (min-width: 768px) {
    .Home__col {
        align-items: flex-start;
    }
}
.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/csswoman/platzi_badges/static/media/stars.877428a1.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
}

.Badges__hero>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Badges__container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 0 1rem;
}

.Badges_conf-logo {
    margin-bottom: 2rem;
}

.Badges__buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
}
.BadgesList ul>li {
    margin-bottom: 1rem;
}

.BadgesListItem {
    background: #FFFFFF;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.23);
    border-radius: 5px;
    display: flex;
    padding: 1rem;
    margin: 20px;
}

.BadgesListItem__avatar {
    border-radius: 50%;
    background: #a3a3a3;
    margin-right: 0.5rem;
    width: 80px;
    height: 80px;
}

.Badges_buttons {
    display: flex;
    justify-content: flex-end;
}
.PageLoading {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
}
.lds-grid {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}

.lds-grid div {
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #000;
    -webkit-animation: lds-grid 1.2s linear infinite;
            animation: lds-grid 1.2s linear infinite;
}

.lds-grid div:nth-child(1) {
    top: 6px;
    left: 6px;
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

.lds-grid div:nth-child(2) {
    top: 6px;
    left: 26px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
}

.lds-grid div:nth-child(3) {
    top: 6px;
    left: 45px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
}

.lds-grid div:nth-child(4) {
    top: 26px;
    left: 6px;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
}

.lds-grid div:nth-child(5) {
    top: 26px;
    left: 26px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
}

.lds-grid div:nth-child(6) {
    top: 26px;
    left: 45px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
}

.lds-grid div:nth-child(7) {
    top: 45px;
    left: 6px;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
}

.lds-grid div:nth-child(8) {
    top: 45px;
    left: 26px;
    -webkit-animation-delay: -1.2s;
            animation-delay: -1.2s;
}

.lds-grid div:nth-child(9) {
    top: 45px;
    left: 45px;
    -webkit-animation-delay: -1.6s;
            animation-delay: -1.6s;
}

@-webkit-keyframes lds-grid {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0.5;
    }
}

@keyframes lds-grid {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: 0.5;
    }
}
.PageError {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    width: 100%;
}
.BadgeNew__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/csswoman/platzi_badges/static/media/stars.877428a1.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
    color: #ffffff;

    display: flex;
    justify-content: center;
}

.BadgeNew__hero-image {
    max-height: 100px;
}
.Badge {
    background: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
    height: 380px;
}

.Badge__header {
    padding: 0.5rem 0;
    height: 80px;
    background-image: linear-gradient(to right, #161616, #191b1f, #182128, #13272f, #0a2e33, #073335, #063837, #083d37, #08413b, #074540, #074a44, #064e49);
    display: flex;
    justify-content: center;
}

.Badge__section-name {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
}

.Badge__section-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 0;
    background: #F4F4F7;
}


.Badge__avatar {
    border-radius: 50%;
    margin-right: 1rem;
    width: 120px;
    height: 120px;
    object-fit: cover;
}

.Badge__footer {
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #98CA3F;
    font-weight: bold;
    font-style: italic;
}
.BadgeDetails__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/csswoman/platzi_badges/static/media/stars.877428a1.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
    color: #ffffff;
}

.BadgeDetails__hero-attendant-name {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.BadgeDetails__buttons {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1rem;
}
.Modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);

    display: flex;
    flex-direction: column;
    align-items: center;
}

.Modal__container {
    position: relative;
    top: 10rem;
    background-color: #ffffff;
    padding: 1rem;
    width: 400px;
}

.Modal__close-button {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background-color: #F5F5F5;
    padding: 0.5rem 1rem;
}
.BadgeEdit__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/csswoman/platzi_badges/static/media/stars.877428a1.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
    color: #ffffff;

    display: flex;
    justify-content: center;
}

.BadgeEdit__hero-image {
    max-height: 100px;
}
